.cta {
    background: #ac9376;
    padding: 40px 0;
  }
  
  .cta h3 {
    color: #fff;
    font-size: 28px;
    font-weight: 500;
  }
  
  .cta p {
    color: black;  
    font-size: 17px;
  
  }
  
  .cta .cta-btn {
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 12px 30px;
    border-radius: 50px;
    transition: 0.5s;
    margin: 10px;
    color: #fff;
    background: #ef6603;
  }
  
  .cta .cta-btn:hover {
    background: #fff;
    color: #ef6603;
  }
  
  @media (max-width: 1024px) {
    .cta {
      background-attachment: scroll;
    }
  }
  
  @media (min-width: 769px) {
    .cta .cta-btn-container {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }
    

  
